import { Component, OnInit } from '@angular/core';
import {DucksurveyService} from '../ducksurvey.service';
import { DuckFeed } from '../duckfeed';

@Component({
  selector: 'app-survey-report',
  templateUrl: './survey-report.component.html',
  styleUrls: ['./survey-report.component.scss']
})
export class SurveyReportComponent implements OnInit {
  duckfeed:DuckFeed[] =[];

  constructor(
    private duckService: DucksurveyService
  ) { }

  ngOnInit() {
    this.retriveSurveyReport()
  }

  retriveSurveyReport() {
    this.duckService.getSurveyReport().subscribe(
      result => {   
        console.log(result);     
        this.duckfeed = result;
        
      })
  }

}
