import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http'
import { Observable, throwError } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';
import { DuckFeed } from './duckfeed'

@Injectable({
  providedIn: 'root'
})
export class DucksurveyService {

  private surveyUrl = 'http://192.168.99.100:41961/survey/';

  constructor( 
    private http: HttpClient
  ) { }

  createSurveyData(surveyData: DuckFeed ) : Observable<DuckFeed[]> {
    const headers = new HttpHeaders({'Content-Type': 'application/json'});
    return this.http.post<DuckFeed[]>(this.surveyUrl+'duck',surveyData, {headers})
    .pipe(
      tap(datapoint => console.log("duckreport:", JSON.stringify(datapoint))),
      catchError(this.handleError)      
    );    
  }

  getSurveyReport() : Observable<DuckFeed[]> {
    const headers = new HttpHeaders({'Content-Type': 'application/json'});
    return this.http.get<DuckFeed[]>(this.surveyUrl+'surveyreport', {headers})
    .pipe(
      tap(datapoint => console.log("Duck Report:", JSON.stringify(datapoint))),
      catchError(this.handleError)
    )
  }

  private handleError(err: any) {
    let errorMessage: string;
    if (err.error instanceof ErrorEvent) {
      // A client-side or network error occurred
      errorMessage = `An error occurred: ${err.error.message}`;
    } else {
      // The backend returned an unsuccessful response code
      errorMessage = `Backend returned code ${err.status}: ${err.body.error}`;
    }
    console.error(err);
    return throwError(errorMessage);
  }


}
