import { Component, OnInit, ViewChild } from '@angular/core';
import { DucksurveyService } from '../ducksurvey.service';
import { DuckFeed } from '../duckfeed';
import { FormGroup, FormControl, Validators, NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-survey-form',
  templateUrl: './survey-form.component.html',
  styleUrls: ['./survey-form.component.scss']
})
export class SurveyFormComponent implements OnInit {
  @ViewChild(NgForm, { static: true }) surveyForm: NgForm;
  duckSurveyForm: FormGroup;
  duckfeed:DuckFeed;
  successMessage: any; errorMessage: any;

  // get isDirty() : boolean {
  //   return this.surveyForm.dirty ? true : false ;
  // }

  constructor(
    private duckservice: DucksurveyService,
    private router:Router,
    private toaster: ToastrService
  ) { }

 

  ngOnInit() {
    this.duckSurveyForm = new FormGroup({
      foodName: new FormControl('', Validators.required),
      location: new FormControl('', Validators.required),
      totalNoOfDucks: new FormControl('', [Validators.required, Validators.pattern("^[0-9]*$")]), //Allow entry of numbers only
      amountFed: new FormControl('', Validators.required),
      feedingTime: new FormControl('', Validators.required)
  
    })
  }

  saveSurveyData() {
    if(this.duckSurveyForm.valid){
      if(this.duckSurveyForm.dirty){
        //copying the form values to the local model object
        const surveyData = {...this.duckfeed, ...this.duckSurveyForm.value};
        this.duckservice.createSurveyData(surveyData).subscribe(
          (success) => {
            this.onSuccess();
            this.onSaveComplete();
          },
          (error) => {
            this.onFailure();
          }
        )
      }
    }
    else{
      this.errorMessage = "Please correct the errors in the form";
    }
  }

//Toaster message for a successful server response
  onSuccess(): void {
    this.toaster.success('Your survey is valuable for us!','Thank you!', {
      timeOut: 5500,
      closeButton: true,
      progressBar: true
    });
  }

  //Toaster message for an unsuccessful server response
  onFailure(): void {
    this.toaster.error('Please try again Later!', 'Sorry!', {
      timeOut: 5500,
      closeButton: true,
      progressBar: true
    })
  }

  onSaveComplete(): void {
    // Reset back to pristine
    this.duckSurveyForm.reset(this.duckSurveyForm.value);

    // Navigate to the report page
    this.router.navigate(['/report']);
  }

}
